export default {
  props: {
    p: {
      type: Object,
      required: true,
    },
  },
  computed: {
    language() {
      return this.$store.state.manager.language;
    },
  },
  methods: {
    cle() {
      this.$store.dispatch("requests/cancelRequest", {
        id: this.p.id,
      });
    },
    logging() {
      console.log(`this ID = ${this.p.id}\n`);
    },
  },
};
